$tablet-width: 768px;
$desktop-width: 1200px;
$mobile-width-only: 767px;

.head {
    margin: 0;
}

.page {
    height: 100%;
    @media (min-width: $desktop-width) {
        width: 100%;
    }
    }


.page__body {
        height: 100%;
        margin: 0;    
}

.page-header {    
    align-items: center;
    justify-content: center;
    padding-left: 20px;
    padding-right: 20px;
    @media (min-width: $tablet-width) {
          
            
        padding-left: 30px;
    padding-right: 30px;
        .main-nav__toggle {
            display: none;
        }
    }
    @media (min-width: $desktop-width) {
        display: grid;
        grid-template-columns: 1fr 1fr;
        padding-left: 110px;
        padding-right: 110px;
        margin-left: auto;
        margin-right: auto;
        max-width: 2000px; 
    }
    
   
}

.page-header__wrapper { 
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    min-height: 66px;
    margin-top: 0;
    @media (min-width: $tablet-width) {   
        height: auto;
        }
    @media (min-width: $desktop-width) {   
        width: 202px;
        height: auto;
    }    
}

.page-header__logo__img__tablet {
    margin-right: 93px;
    margin-top: 24px;
    @media (max-width: $mobile-width-only) {   
    display: none;    
    }
    @media (min-width: $desktop-width) {   
        width: 202px;
        height: 59px;
        margin-right: 0;
        margin-top: 53px;
        &:hover,
        &:focus {
            opacity: 0.8;
        }
        &:active {
            opacity: 0.6;
        }
        }  
    }
    
.page-header__logo__img {
    cursor: pointer;
    &:hover {
        opacity: 0.8;
    }
    &:focus {
        opacity: 0.8;
    }
    &:active {
        color: black;
        opacity: 0.6;
        background-color: white;
    }
    @media (min-width: $tablet-width) {   
        display: none;
        }
    @media (min-width: $desktop-width) {   
            display: none;
            }    
}


.page-header__toggle {
    border: none;
}

