

.visually-hidden {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;
    white-space: nowrap;
    clip-path: inset(100%);
    clip: rect(0 0 0 0);
    overflow: hidden;
  }

  img {
    max-width: 100%;
    //height: auto;
  }


  .button {
    box-sizing: border-box;
    background: #68B738;
    text-decoration: none;
    color: var(--basic-white, #FFF);
    font-family: Oswald;
    font-style: normal;
    text-transform: uppercase;
    
  }

  .button:hover,
  .button:focus {
    background: #5EAA2F;
  }

  .button:active {
    background: #5EAA2F;
    span {opacity: 0.3;
    }
  }

  a {
    text-decoration: none;
    text-transform: uppercase;
    padding: 0;
    
  }

  input {
    &:hover {
        border: 1px solid var(--special_grey, #CDCDCD);
    }
    &:focus {
        border: 2px solid var(--special_dark-grey, #444);
    }
    &:active {
        border: 2px solid var(--status_error, #FF8282);
    }
  }

  input[type='radio']:after {
    width: 18px;
    height: 18px;
    border-radius: 18px;
    top: 5px;
    left: 4px;
    position: relative;
    background-color: #d1d3d1;
    content: '';
    display: inline-block;
    visibility: visible;
    border: 2px solid white;
}

input[type='radio']:checked:after {
    width: 18px;
    height: 18px;
    border-radius: 18px;
    top: 5px;
    left: 4px;
    position: relative;
    background-color: #68B738;
    content: '';
    display: inline-block;
    visibility: visible;
    border: 2px solid white;
    
}

input[type="checkbox"] {
    accent-color: white; /* Green color */
    border: 2px solid #444;
  }